/* Background effect. It covers the entire site */


.modal {
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Overlay effect: translucent background: black w/ partial opacity */
    z-index: 9999; /* Overlay effect: positioned over other containers */
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    position: fixed; /* Fix position on the top-left corner*/
    top: 0;
    left: 0;
    overflow: auto; /* Enable scroll if needed */
    padding-top: 180px; 
    display: block;
    opacity: 0;
    -webkit-animation: show-modal 0.6s forwards;
            animation: show-modal 0.6s forwards;
    
  }

  .modal-background {
    background-image: url(/Course-Searcher/static/media/modalwall.856a10a9.jpg);
    height: 60%; /* Height in proportion to its parent container */
    margin: auto; /* Auto margin according to the element width */
    width: 70%;
    margin: auto;
    max-width: 640px;
    position: relative;
    /* border-radius: 20px; */
  }
  
  /* Modal content */
  .modal-content {
    position: absolute;
    top: 50%; left: 50%;
    transform: translate(-50%,-50%);
    background-color: rgba(255, 255, 255, 0.8);
    width: 80%; 
    max-width: 640px; /* Max width where it stops expanding */
    height: 70%; 
    margin: 0 auto;
    padding: 30px;
    /* border: 1px solid black; */
    border-radius: 0; /* Optional. Rounds container corners */
    font-family: 'Open Sans', sans-serif;
    box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.02), 0 6.7px 5.3px rgba(0, 0, 0, 0.028), 0 12.5px 10px rgba(0, 0, 0, 0.035), 0 22.3px 17.9px rgba(0, 0, 0, 0.042), 0 41.8px 33.4px rgba(0, 0, 0, 0.05), 0 100px 80px rgba(0, 0, 0, 0.07);
    /* animation: open-description 0.6s 0.2s cubic-bezier(0.745, 0.045, 0.255, 1) forwards; */
    z-index: 2;
  }
  
  .content-matter {
    font-size: 17px;
    text-align: left;
    white-space:pre;
  }
  tab{
    white-space: pre;
  }
/* 
  .space{
    display: inline-block;
    margin-left: 45px !important;
  } */

  .modal-flex {
    display: flex;
    height: 100%; 
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  .modal-info {
    background-color: #888;
    border: 1px solid #888;
  }
  
  /* Close button */
  .close {
    color: #aaaaaa;
    /* float: right !important; */
    font-size: 40px;
    font-weight: bold;
    text-align: right;
    padding: 0.5em 1em;
    z-index: 999 !important;
    position: relative;
  }
  
  .close:hover,
  .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }
  
